:root {
  --logo-theme-dark: #fff;
  --logo-theme-light: #494848;
  --subtitle-color: #b4b4b4;
  --accent-color: #f3c653;
  --primary-text-color: #5f5f5f;
  --secondary-text-color: #7a7a7a;
  --accent-text-color: #c79002;
  --logo-font: "Film P3";
  --main-font: "Montserrat";
  --additional-font: "Times New Roman";
  --background-lighter: #fffdee;
  --background-darker: #f0ecd6;
  --footer-background: #0e0c07;
  --transition-fast: 250ms cubic-bezier(.4, 0, .2, 1);
  --transition-slow: 500ms cubic-bezier(.4, 0, .2, 1);
}

header {
  z-index: 3;
  width: 100%;
  padding-top: 16px;
  position: absolute;
  top: 0;
  right: 0;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__logo {
  letter-spacing: .3em;
  color: var(--logo-theme-dark);
  transition: var(--transition-slow);
  font-family: Film P3;
  font-size: 36px;
  font-weight: 700;
  line-height: 1.08333;
  text-decoration: none;
  display: block;
}

.header__logo:hover, .header__logo:focus {
  transform: scale(1.2);
}

.header__nav-list {
  align-items: center;
  gap: 20px;
  display: flex;
}

.header__nav-link {
  letter-spacing: .02em;
  color: var(--accent-color);
  transition: var(--transition-slow);
  align-self: center;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
}

.header__tel-icon {
  fill: var(--accent-color);
  transition: var(--transition-slow);
}

