  .header__logo {
    font-size: 32px;
  }

  .header__nav-link {
    font-size: 14px;
  }

  .header__nav-list {
    gap: 10px;
  }
}

.header__nav-items {
  gap: 6px;
  display: flex;
}

.header__nav-tel {
  color: var(--logo-theme-dark);
  transition: var(--transition-slow);
  line-height: 0;
  display: flex;
}

.header__tel-number {
  transition: var(--transition-slow);
  align-self: center;
  display: none;
}

